import Vue from 'vue';
import messageComponent from './src/index.vue' // 
let MessageConstructor = Vue.extend(messageComponent); // messagenew

let instance = null // 
let count = 0 // 

const MyMessage = function (options) {
    if (options.duration & typeof options.duration !== 'number') { // duration
        console.error('Error! duration Must be a numeric type ') // 
        return
    }
    count = count + 1 // MyMessage
    instance = new MessageConstructor({ // 
        data: options, // datadata
        propsData: { // propsData
            count: count, // 
            cutCount: cutCount // MyMessage
        },
    });
    instance.$mount(); // 
    document.body.appendChild(instance.$el); // domdocument
    instance.isShowMyMessage = true; // isShowMyMessagetrue
    return instance; // MyMessage
}

function cutCount() { // message
    count = count - 1 // 
    let messageBoxDomList = document.querySelectorAll('.messageBox') // messageDOM
    for (let i = 0; i < messageBoxDomList.length; i++) { // DOM
        let dom = messageBoxDomList[i] // 60
        dom.style['top'] = parseInt(dom.style['top']) - 60 + 'px'
    }
}

export default MyMessage // 
