<template>
  <transition name="message-fade" @after-leave="handleAfterLeave">
    <div
        v-show="isShowMyMessage"
        :class="[
        'messageBox',
        center ? 'horizontal' : '',
        typeArr.includes(type) ? type : '',
      ]"
        :style="controlTop"
        @mouseenter="clearTimerFn"
        @mouseleave="startTimerFn"
    >
      <span> {{ message }}</span>
    </div>
  </transition>
</template>

<script>
export default {
  name: "VuxMessage",
  props: {
    count: {
      // 
      type: Number,
      default: 1,
    },
    cutCount: {
      // dom
      type: Function,
    },
  },
  data() {
    return {
      isShowMyMessage: false, // v-show
      message: "", // 
      timer: null, // 
      duration: 3000, // 3
      center: false, // false
      type: "info", // info
      typeArr: ["info", "success", "warning", "error"], // 4
    };
  },
  computed: {
    controlTop() {
      return {
        // message
        top: `${12 + (this.count - 1) * 60}px`,
      };
    },
  },
  mounted() {
    this.startTimerFn(); // 3
  },
  methods: {
    // dom
    startTimerFn() {
      // 0
      if (this.duration > 0) {
        this.timer = setTimeout(() => {
          this.close(); // notice
        }, this.duration);
      }
    },
    // domdom
    clearTimerFn() {
      clearTimeout(this.timer);
    },
    // dom
    handleAfterLeave() {
      // domcountdom
      this.cutCount();
      // dom
      this.$destroy(true);
      this.$el.parentNode.removeChild(this.$el);
    },
    // dom
    close() {
      this.isShowMyMessage = false;
      /**
       * v-showfalsehandleAfterLeave
       * close this.handleAfterLeave()
       * */
    },
  },
};
</script>

<style lang="less" scoped>
// 
.messageBox {
  min-width: 160px;
  height: auto; // 
  padding: 8px; // 
  border: 1px solid #e9e9e9;
  position: fixed; // 
  z-index: 9999;
  top: 20px;
  left: 50%;
  transform: translateX(-50%); // 
  box-sizing: border-box;
  border-radius: 4px; // 
  background-color: #edf2fc;
  // 
  transition: opacity 0.3s, transform 0.4s, top 0.4s;
  display: flex; // 
  align-items: center;
}

// 
.horizontal {
  justify-content: center;
}

.success {
  border: 1px solid #aee884;
  background-color: #f5ffea;
}

.info {
  border: 1px solid #86cfff;
  background-color: #e2f6ff;
}

.warning {
  border: 1px solid #ffe38c;
  background-color: #fffae2;
}

.error {
  background-color: #fff0ee;
  border: 1px solid #ffccc7;
}

// 
.message-fade-enter,
.message-fade-leave-active {
  opacity: 0;
  -webkit-transform: translate(-50%, -100%);
  transform: translate(-50%, -100%);
}



</style>
